.Recipe_App {
  font-family: Georgia, 'Times New Roman', Times, serif;
 
  padding: 10px;
  width:100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
  /* background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%); */
  /* background-image: linear-gradient(to right, #fa709a 0%, #fee140 100%); */
  /* background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%); */

}
h1{
  color:darkred
}

.search_form {
  display: flex;
  justify-content: center;
  width:80%;
  flex-direction: row;
  padding: 30px;

  
  
}

.search_bar {
  
  width:40%;
  border: none;
  border-radius: 10px;
}
.search_button {
  background-color: goldenrod;
  border: none;
  height: 32px;
  width:15%;
  border-radius: 10px;
  font-size: large;
}
.recepiesConatiner {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  justify-content: space-around;
}
.rContainer {
 
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 30%;
 
  background-color:rgb(43, 136, 151);
  border-radius: 15px;
 
  padding: 10px;
  margin: 10px;
  box-shadow: 5px 10px 8px 1px #3b3636;
}
img {
  width: 60%;
}
h3 {
  text-transform: uppercase;
  text-decoration: underline;
  color: rgb(118, 50, 7);
}


@media (max-width:600px){
  .recepiesConatiner{
    flex-direction: column;
    /* justify-content: center; */
    
  }
  .rContainer{
    width:70%;
  }
}li{
  margin-bottom: 5px;
}
